import React, { useEffect, useState } from "react";
import { Line } from "react-chartjs-2";
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from "chart.js";

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend);

// üîπ Fƒ±rƒ±n ID'leri yerine g√∂sterilecek isimleri burada belirliyoruz
const furnaceNames = {
  1: "Giri≈ü 1",
  2: "1. B√∂lge",
  3: "2. B√∂lge",
  4: "Giri≈ü 2",
  5: "Soƒüutma",
  6: "3. B√∂lge",
  7: "4. B√∂lge Alt",
  8: "4. B√∂lge √úst",
  9: "5. B√∂lge Alt",
  10: "5. B√∂lge √úst",
  11: "6. B√∂lge Alt",
  12: "6. B√∂lge √úst",
  13: "Kritik Soƒüutma",
  14: "√áƒ±kƒ±≈ü 1",
  15: "√áƒ±kƒ±≈ü 2",
  16: "√áƒ±kƒ±≈ü 3"
};

// üîπ Her fƒ±rƒ±n i√ßin farklƒ± renkler belirliyoruz
const colors = [
  "#FF5733", "#33FF57", "#3357FF", "#FF33A1", "#FF8C33", "#8C33FF",
  "#33FFD5", "#D533FF", "#FFD533", "#33A1FF", "#FF3361", "#61FF33",
  "#D5FF33", "#33FF8C", "#FF33D5", "#3361FF"
];

const TrendChart = () => {
  const [graphData, setGraphData] = useState([]);

  useEffect(() => {
    fetch("/api/getData")
      .then((res) => res.json())
      .then((data) => {
        // üîπ Fƒ±rƒ±nlarƒ± ID'ye g√∂re sƒ±ralƒ±yoruz
        const sortedData = data.sort((a, b) => a.furnaceId - b.furnaceId);
        setGraphData(sortedData);
      });
  }, []);

  return (
    <div className="w-full max-w-5xl mx-auto p-2">
      <h1 className="text-3xl font-bold text-center mb-2 dark:text-white">Fƒ±rƒ±n Sƒ±caklƒ±k Takibi</h1>
      
      <div className="grid grid-cols-1 gap-6">
        {graphData.map((furnace, index) => (
          <div key={furnace.furnaceId} className="p-1 bg-white dark:bg-gray-900 rounded-xl shadow-md">
            <h2 className="text-xl font-semibold text-center mb-1 dark:text-white">
              {furnaceNames[furnace.furnaceId] || `Fƒ±rƒ±n ${furnace.furnaceId}`} {/* üîπ ID yerine isim g√∂steriyoruz */}
            </h2>
            <div className="w-full h-30">
              <Line
                data={{
                  labels: furnace.data.map(d => new Date(d.timestamp).toLocaleTimeString()),
                  datasets: [
                    {
                      label: furnaceNames[furnace.furnaceId] || `Fƒ±rƒ±n ${furnace.furnaceId}`,
                      data: furnace.data.map(d => d.temperature),
                      borderColor: colors[index % colors.length],
                      backgroundColor: colors[index % colors.length] + "30",
                      borderWidth: 2,
                      pointRadius: 3,
                      fill: false,
                    },
                  ],
                }}
                options={{
                  responsive: true,
                  maintainAspectRatio: false,
                  plugins: {
                    legend: { display: false },
                  },
                  scales: {
                    y: {
                      beginAtZero: true,
                      suggestedMax: 100,
                      ticks: { color: "#ffffff" },
                    },
                    x: { ticks: { color: "#ffffff" } },
                  },
                }}
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TrendChart;
